import styled from 'styled-components';

export const PageWrapper = styled.div`
  max-width: 1100px;
  margin: 0 auto;

  @media screen and (max-width: 768px) {
    margin-top: 60px;
    padding: 0 35px;
    .page-title {
      font-size: 36px;
    }
  }

  @media screen and (max-width: 425px) {
    padding: 0;
    .page-title {
      justify-content: center;
    }
  }
`;

export const Button = styled.button`
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #e2ae39;
  margin-left: 20px;
  img {
    width: 100%;
  }
  &:hover {
    background-color: #e2ae39;
  }
  @media screen and (max-width: 425px) {
    width: 30px;
    height: 30px;
    margin-left: 15px;
  }
`;
export const HeaderWrapper = styled.div`
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding-bottom: 48px;
  border-bottom: 1px solid #fff;
  margin-bottom: 110px;

  @media screen and (max-width: 768px) {
    flex-flow: column;
    align-items: center;
    margin-bottom: 50px;
  }

  @media screen and (max-width: 425px) {
    margin-bottom: 44px;
  }
`;

export const WalletTokenView = styled.div`
  padding: 25px 40px;
  background: #e2ae39;
  border-radius: 10px;

  .amount-title {
    color: #fff;
    font-weight: 700;
    font-size: 32px;

    @media screen and (max-width: 991px) {
      font-size: 24px;
    }
  }

  .amount-value {
    font-size: 64px;
    color: #000;
    margin: 0;
    text-align: center;
    font-weight: 700;

    @media screen and (max-width: 991px) {
      font-size: 36px;
    }
  }
  @media screen and (max-width: 991px) {
    width: 328px;
  }
  @media screen and (max-width: 768px) {
    padding: 21px 12px;
    text-align: center;
    &:first-of-type {
      margin-bottom: 30px;
    }
  }
  @media screen and (max-width: 425px) {
    width: 295px;
  }
`;

export const CollectCardRow = styled.div`
  display: flex !important;
  width: 100%;
  flex-wrap: wrap;
  gap: 40px 56px;
  width: 968px;
  margin: 0 auto;

  @media screen and (max-width: 768px) {
    width: 255px;
    gap: 40px 31px;
  }

  @media screen and (max-width: 425px) {
    width: 100%;
    justify-content: space-around;
    gap: 40px 0;
  }
`;

export const CollectionWrapper = styled.div`
  padding-bottom: 40px;
  border-bottom: 1px solid #fff;
  margin-bottom: 50px;

  h6 {
    word-break: break-all;
  }

  &:last-of-type {
    border-bottom: none;
  }
`;
