import styled from 'styled-components';

const Image = styled.img`
  width: 200px;
  height: 200px;
  border-radius: 25px;
  margin-bottom: 24px;

  @media screen and (max-width: 768px) {
    width: 112px;
    height: 112px;
  }
`;

const CardText = styled.h6`
  font-size: 28px;
  color: white;
  text-align: center;

  @media screen and (max-width: 768px) {
    font-size: 18px;
  }
`;

const Wrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: center;
  width: 200px;
  @media screen and (max-width: 425px) {
    width: 45%;
  }
`;

interface CollectCardProps {
  src: string;
  name: string;
  rank?: string;
}

const CollectCard = ({ src, name, rank }: CollectCardProps) => {
  return (
    <Wrapper>
      <Image className="card-img" src={src} alt={name} />
      <CardText>{name}</CardText>
      {rank && <CardText>Rank #{rank}</CardText>}
    </Wrapper>
  );
};

export default CollectCard;
